import React, { Component } from 'react';
import Page from '../layouts/main';
import styled from 'styled-components';

import MapSelector from '../components/callouts/map_selector';
import CalloutViewer from '../components/callouts/callout_viewer';

//
// -- Styles
const CalloutWrapper = styled.div`
	display: flex;
	> div {
		flex: 1;
	}
`;

class Callouts extends Component {
	state = {
		active_map: '',
		timer: 5
	};

	componentDidMount() {
		this.startTimer();
	}

	startTimer = () => {
		this.incrementer = setInterval(() => {
			if (this.state.timer === 0) {
				this.setState({ timer: 5 });
			} else {
				this.setState({ timer: this.state.timer - 1 });
			}
		}, 1000);
	};

	stopTimer = () => {
		clearInterval(this.incrementer);
	};

	setActiveMap = map_name => {
		this.setState({ active_map: map_name });
	};

	render() {
		return (
			<Page>
				<CalloutWrapper>
					<MapSelector
						setActiveMap={this.setActiveMap}
						activeMap={this.state.active_map}
					/>
					<CalloutViewer activeMap={this.state.active_map} />
					<h1>{this.state.timer}</h1>
					<button onClick={this.stopTimer}>stop</button>
					<button onClick={this.startTimer}>stop</button>
				</CalloutWrapper>
			</Page>
		);
	}
}

export default Callouts;
